@import url(https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@700&display=swap);
@font-face {
  font-family: "Founders Grotesk";
  src: url(/dashboard/static/media/FoundersGrotesk-Regular.5e8e49c3.otf) format("otf");
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Founders Grotesk", sans-serif;
}

.container {
  /* margin-right: auto;
  margin-left: auto;
  max-width: 960px; */
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  /* padding-right: 10px;
  padding-left: 10px; */
}

.dashboard,
.analytics,
.balances,
.customers,
.aboutus {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
  padding: 0 25px;
  background: #ffffff;
  margin-left: 280px;
  transition: 350ms;
}

.closed {
  margin-left: 0px;
  transition: 850ms;
}

.header {
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  padding: 0 0 0 18px;
  line-height: 22px;
  color: #2a2a2a;
}

.date {
  width: 124px;
  height: 14px;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  color: #a6abb2;
}

.card-container {
  -webkit-flex-direction: row;
          flex-direction: row;
  display: -webkit-flex;
  display: flex;
  max-width: 720px;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 60px;
}

@media screen and (max-width: 600px) {
  .card-container {
    height: 380px;
  }
}

.navbar {
  background-color: #fff;
  height: 80px;
  width: 280px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
}

.menu-bars {
  margin-left: 2rem;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  background: "none";
}

.nav-menu {
  background-color: #ebebeb;
  width: 280px;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  position: fixed;
  top: 0;
  left: -280px;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  padding: 8px 0px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #a6abb2;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  width: 100%;
  height: 52px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 40px;
}

.nav-text a:hover {
  background-color: #FFF;
}

.selected {
  color: #000 !important;
  background-color: #F5F5F5;
}
.nav-menu-items {
  width: 280px;
}

.navbar-toggle {
  background-color: #e5e5e5;
  width: 280px;
  height: 80px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
}

span {
  margin-left: 16px;
}

.logo {
  padding: 40px;
}

.active {
  color: #000;
}

.menu-subheading {
  font-family: "Space Grotesk", "sans-serif";
  font-style: normal;
  font-weight: 600;
  font-size: 14px !important;
  line-height: 17px;
  color: #2a2a2a;
  padding: 15px 40px;
}


select {
    margin: 5px;
    padding: 5px;
    font-size: 14px;
    border: none;
    height: 34px;
    background: #f5f5f5;
    border-radius: 5px;
  }
  
  .option{
      border-bottom: 1px solid black;
  }
.card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 176px;
  width: 340px;
  border: 1px solid #ebebeb;
  border-radius: 10px;
  padding: 10px 0px;
}

.left-card {
  border-radius: 10px 0 0 10px;
}

.right-card {
  left: -7px;
  position: relative;
}
.card-header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 18px;
  height: 50px;
}

.card-heading {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */
  color: #2c665d;
}

.card-content {
  padding: 30px 20px;
}
.amount {
  font-family: Space Grotesk;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 43px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  letter-spacing: 0.1px;
  color: #2a2a2a;
}

.divider {
  border: 0.1px solid rgba(128, 128, 128, 0.24);
}

@media screen and (max-width: 600px) {
  .right-card {
    left: 0px;
    position: unset;
  }

  .left-card {
    border-radius: 10px;
  }
}

.conversion-rate {
  font-style: normal;
  font-weight: 100;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.1px;
  padding: 5px 0px;
  color: #a6abb2;
}

.grey{
  color: #2A2A2A;
}
.table-container {
  margin-top: 50px;
  padding-left: 20px;
  padding-right: 20px;
}

.responsive-table li {
  border-radius: 3px;
  padding: 10px 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-bottom: 15px;
  transition: 850ms;
}
.responsive-table .table-row:hover {
  border: 1px solid #2c665d;
  transition: 350ms;
  box-sizing: border-box;
  box-shadow: 0px 2px 30px rgba(41, 40, 40, 0.08);
}
.responsive-table .table-header {
    background-color: #f5f5f5;
    font-size: 16px;
    height: 48px;
    letter-spacing: 0.03em;
}
.responsive-table .table-row {
  background-color: #ffffff;
  max-height: 300px;
  font-weight: 300;
  box-shadow: 0px 1px 0px #ebebeb;
}
.responsive-table .col-4 {
  padding: 7px;
  margin: 5px;
}

.table-header {
  /* font-family: 'Space Grotesk', sans-serif; */
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.1px;
  color: #2a2a2a;
  padding: 7px;
  margin: 5px;
}
@media all and (max-width: 600px) {
  .table-container {
    display: block;
    margin: 50px auto;
    max-width: 400px;
    width: 380px;
    padding-left: 20px;
    padding-right: 20px;
  }
  .responsive-table .table-header {
    display: none;
  }
  .responsive-table li {
    display: block;
    padding: 5px;
  }
  .responsive-table .col {
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
  }
  .responsive-table .col {
    display: -webkit-flex;
    display: flex;
    padding: 10px 0;
  }
  .responsive-table .col:before {
    color: #6c7a89;
    padding-right: 10px;
    content: attr(data-label);
    -webkit-flex-basis: 30%;
            flex-basis: 30%;
  }
  .responsive-table .table-row {
    box-shadow: 0px 0px 9px 0px rgb(0 0 0 / 10%);
  }
}

